import { environment } from './../environments/environment';
import { Component, OnInit, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { FormGroup, FormControl, FormBuilder, Validator, Validators } from '@angular/forms';
import { Loan } from './interface/loan';
import * as _ from 'underscore';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
	serverData: JSON;
	title = 'risk';
	form: FormGroup;
	loan: Loan;
	baseUrl: string;
	options: any;
	interest: number[];

	constructor(private http: HttpClient, private fb: FormBuilder) {
		this.baseUrl = environment.base_url;
		this.loan = {
			Principle: undefined,
			Interest: undefined,
			BadDebtRatio: undefined,
			RealX: undefined,
			RealY: undefined,
			PredictX: undefined,
			PredictY: undefined,
		};
		this.createForm();
		this.interest = _.range(0.05, 0.32, 0.01);

	}
	onCreateGraph(xs, real_ys, predict_ys): void {
		this.options = {
			title: {
				show: true,
				text: "Model prediction",
				padding: 5,
				left: "center",
				top: 20
			},
			legend: {
				data: ['Model Predict'],
				align: 'left'
			},
			tooltip: {},
			xAxis: {
				data: xs,
				silent: false,
				splitLine: {
					show: false
				}
			},
			yAxis: {
			},
			series: [
			// 	{
			// 	name: 'From Source',
			// 	type: 'bar',
			// 	data: real_ys,
			// 	animationDelay: function (idx) {
			// 		return idx * 10;
			// 	}
			// }, 
			{
				name: 'Model Predict',
				type: 'bar',
				data: predict_ys,
				animationDelay: function (idx) {
					return idx * 10 + 100;
				}
			}],
			animationEasing: 'elasticOut',
			animationDelayUpdate: function (idx) {
				return idx * 5;
			}
		}
	}

	ngOnInit(): void {
		this.sayHi();
		this.form.get('Principle').valueChanges.subscribe(value => {
			this.loan.Principle = value;
		});
		this.form.get('Interest').valueChanges.subscribe(value => {
			this.loan.Interest = value;
		});

		const xAxisData = [];
		const data1 = [];
		const data2 = [];

		for (let i = 0; i < 100; i++) {
			xAxisData.push('category' + i);
			data1.push((Math.sin(i / 5) * (i / 5 - 10) + i / 6) * 5);
			data2.push((Math.cos(i / 5) * (i / 5 - 10) + i / 6) * 5);
		}
		// this.onCreateGraph(xAxisData, data1, data2);

		// this.options = {
		// legend: {
		// 	data: ['bar', 'bar2'],
		// 	align: 'left'
		// },
		// tooltip: {},
		// xAxis: {
		// 	data: xAxisData,
		// 	silent: false,
		// 	splitLine: {
		// 	show: false
		// 	}
		// },
		// yAxis: {
		// },
		// series: [{
		// 	name: 'bar',
		// 	type: 'bar',
		// 	data: data1,
		// 	animationDelay: function (idx) {
		// 	return idx * 10;
		// 	}
		// }, {
		// 	name: 'bar2',
		// 	type: 'bar',
		// 	data: data2,
		// 	animationDelay: function (idx) {
		// 	return idx * 10 + 100;
		// 	}
		// }],
		// animationEasing: 'elasticOut',
		// animationDelayUpdate: function (idx) {
		// 	return idx * 5;
		// }
		// };
	}
	createForm(): void {
		this.form = this.fb.group({
			Principle: ['', Validators.required],
			Interest: ['', Validators.required],
			BadDebtRatio: ['']
		});
	}
	onUpdateForm(): void {
		this.form.setValue({
			Principle: this.loan.Principle,
			Interest: this.loan.Interest,
			BadDebtRatio: this.loan.BadDebtRatio
		});
	}
	onSubmit(): void {
		const loanUrl = this.baseUrl + '/loan';
		const httpOptions = {
			headers: new HttpHeaders({
				'Content-Type': 'application/json'
			})
		};

		console.log(this.loan);

		this.http.post<Loan>(loanUrl, this.loan, httpOptions).subscribe(
			result => {
				this.loan.BadDebtRatio = Number(result.BadDebtRatio);
				this.loan.PredictX = result.PredictX;
				this.loan.PredictY = result.PredictY;
				this.loan.RealX = result.RealX;
				this.loan.RealY = result.RealY;
				this.onUpdateForm();
				this.onCreateGraph(this.loan.PredictX.map((x, index) => this.roundDec(this.interest[index], 2)), this.loan.RealY, this.loan.PredictY);
			}
		);
	}

	sayHi() {
		this.http.get(this.baseUrl).subscribe(data => {
			// this.serverData = data as JSON;
			console.log(data);
		});
	}
	roundDec(nbr, dec_places) {
		var mult = Math.pow(10, dec_places);
		return Math.round(nbr * mult) / mult;
	}
}
